class FixtureService {
    constructor(footballdataFactory, Fixture, constants, notifications, $q) {
        'ngInject';
        this.footballdataFactory = footballdataFactory;
        this.Fixture = Fixture;
        this.constants = constants;
        this.apiKey = constants.footballApiKey;
        this.notifications = notifications;
        this.$q = $q;
    }

    getAllFixturesFromCache(timeFrame, leagueCode) {
        let that = this;
        let url = `http://api.football-data.org/v1/fixtures?apiKey=${this.apiKey}&league=${leagueCode}&timeFrame=${timeFrame}`;

        if ('caches' in window) {
            return this.$q(function(resolve, reject) {
                caches.match(url).then(response => {
                    if (response) {
                        response.json().then(res => resolve(res.fixtures.map(fixture => new that.Fixture(fixture))));
                    } else {
                        return {};
                    }
                });
            });
        }
    }

    getAllFixturesFromRequest(timeFrame, leagueCode) {
        let that = this;

        return this.$q(function(resolve, reject) {
            that.footballdataFactory.getFixtures({ timeFrame: timeFrame, league: leagueCode, apiKey: that.apiKey })
                .then(res => resolve(res.data.fixtures.map(fixture => new that.Fixture(fixture))));
        });
    }


    getFixture(fixtureId) {
        return this.footballdataFactory.getFixture({ id: fixtureId, apiKey: this.apiKey })
            .then(res => res.data);
    }

    returnTime(fixtureDate) {
        let time = Math.floor((new Date()
            .getTime() - new Date(fixtureDate)
            .getTime()) / 60000);
        if (time < 45) {
            return time + "'";
        } else if (time >= 45 && time <= 60) {
            return "Half-time";
        } else if (time >= 60 && time <= 90) {
            return time - 15 + "'";
        } else if (time >= 90) {
            return '90\' +';
        }
    }

    checkForGoal(newFixtures, oldFixtures) {
        let that = this;
        newFixtures.forEach(newFixture => {
            oldFixtures.forEach(oldFixture => {
                if (newFixture.id === oldFixture.id) {
                    if (newFixture.awayTeamGoals !== oldFixture.awayTeamGoals ||
                        newFixture.homeTeamGoals !== oldFixture.homeTeamGoals) {
                        let title = '';
                        if (newFixture.awayTeamGoals !== oldFixture.awayTeamGoals) {
                            title = `Goal! ${newFixture.awayTeamName} scores!`;
                        }
                        if (newFixture.homeTeamGoals !== oldFixture.homeTeamGoals) {
                            title = `Goal! ${newFixture.homeTeamName} scores!`;
                        }
                        let body = `${newFixture.homeTeamName} ${newFixture.homeTeamGoals}:${newFixture.awayTeamGoals} ${newFixture.awayTeamName}`;
                        that.notifications.goalNotification(title, body);
                    }
                }
            });
        });
    }

    checkIfThereIsFixtruesInLeague(fixturesInLeague) {
        if (fixturesInLeague && fixturesInLeague.length === 0) {
            return true;
        } else {
            return false;
        }
    }

    checkIfThereIsDifferenceInData(newFixtures, oldFixtures) {
        if (JSON.stringify(newFixtures) !== JSON.stringify(oldFixtures)) {
            oldFixtures = newFixtures;
        }
        return oldFixtures;
    }

}

export default FixtureService;


