class LeagueController {
    constructor(fixtureService, notifications, $interval) {
        'ngInject';
        this.fixtureService = fixtureService;
        this.name = 'league';
        this.allFixtures = [];
        this.league = {};
        this.timeFrameKey = '';
        this.empty = true;
        this.notifications = notifications;
        this.$interval = $interval;
    }


    $onInit() {
        let that = this;

        this.$interval(function() {
            that.fixtureService.getAllFixturesFromRequest(that.timeFrameKey, that.league.league)
                .then(function(res) {
                    that.fixtureService.checkForGoal(res, that.allFixtures);
                    that.fixtureService.checkIfThereIsDifferenceInData(res, that.allFixtures);
                    return res;
                })
                .then(fixturesInLeague => that.empty = that.fixtureService.checkIfThereIsFixtruesInLeague(fixturesInLeague));
        }, 30000);
    }

    $onChanges(changes) {
        let that = this;

        //make requests if timeFrameKey is changed
        if (changes.timeFrameKey && typeof changes.timeFrameKey.previousValue !== 'object') {
            this.fixtureService.getAllFixturesFromCache(changes.timeFrameKey.currentValue, this.league.league)
                .then(res => this.allFixtures = res)
                .then(fixturesInLeague => this.empty = this.fixtureService.checkIfThereIsFixtruesInLeague(fixturesInLeague));
            this.fixtureService.getAllFixturesFromRequest(changes.timeFrameKey.currentValue, this.league.league)
                .then(res => this.fixtureService.checkIfThereIsDifferenceInData(res, this.allFixtures))
                .then(fixturesInLeague => this.empty = this.fixtureService.checkIfThereIsFixtruesInLeague(fixturesInLeague));
        }

        //make requests on first load and on refresh
        if (changes.league && Object.keys(changes.league.currentValue).length !== 0) {
            this.fixtureService.getAllFixturesFromCache(this.timeFrameKey, changes.league.currentValue.league)
                .then(res => this.allFixtures = res)
                .then(fixturesInLeague => this.empty = this.fixtureService.checkIfThereIsFixtruesInLeague(fixturesInLeague));
            this.fixtureService.getAllFixturesFromRequest(this.timeFrameKey, changes.league.currentValue.league)
                .then(res => this.fixtureService.checkIfThereIsDifferenceInData(res, this.allFixtures))
                .then(fixturesInLeague => this.empty = this.fixtureService.checkIfThereIsFixtruesInLeague(fixturesInLeague));
        }
    }
}

LeagueController.$inject = ['fixtureService', 'notifications', '$interval'];

export default LeagueController;


