class TableController {
    constructor() {
        this.name = 'table';
        this.empty = true;
    }

    $onInit() {
        if (this.table.teams) {
            this.empty = this.table.teams.length > 0 ? false : true;
        }
    }
}

TableController.$inject = [];

export default TableController;


