class TeamDetailsController {
    constructor($window) {
        this.name = 'teamDetails';
        this.$window = $window;
    }

    $onInit() {
        this.$window.scrollTo(0, 0);
    }
}

TeamDetailsController.$inject = ['$window'];

export default TeamDetailsController;


