class LeagueService {
    constructor(footballdataFactory, constants, $q) {
        'ngInject';
        this.footballdataFactory = footballdataFactory;
        this.constants = constants;
        this.apiKey = constants.footballApiKey;
        this.leagueIds = constants.leagueIds;
        this.$q = $q;
    }

    getAllLeaguesFromCache() {
        let that = this;
        if ('caches' in window) {
            return this.$q.all(this.leagueIds.map(leagueId => {
                let url = `http://api.football-data.org/v1/soccerseasons/${leagueId}?apiKey=${this.apiKey}`;
                return caches.match(url).then(response => {
                    if (response) {
                        return response.json().then(res => res);
                    } else {
                        return {};
                    }
                });
            }));
        }
    }

    getAllLeaguesFromRequest() {
        let that = this;

        return this.$q.all(
            that.leagueIds.map(leagueId => {
                return that.footballdataFactory.getSeason({ id: leagueId, apiKey: that.apiKey })
                    .then(res => res.data);
            })
        );
    }
}

export default LeagueService;


